body {
  margin: 0;
}

:root {
  --blue-color: #00acdc;
}

a {
  display: block;
}

.header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin: 0;
  padding: 0 50px;
  font-family: Roboto, sans-serif;
  font-weight: 500;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

* {
  box-sizing: border-box;
}

.logo img {
  width: 40px;
  height: 40px;
  margin-top: 5px;
}

.nav__list {
  align-items: center;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
}

.li {
  margin-left: 20px;
}

.li:first-child {
  margin-left: 0;
}

.is-active {
  color: var(--blue-color);
  position: relative;
}

.nav__link.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav__link:hover {
  color: var(--blue-color);
}
